html {
  font-size: 62.5%;
}

body {
  font-size: 1.44rem;
  font-family: "メイリオ", "Meiryo", "ＭＳ ゴシック", "Hiragino Kaku Gothic ProN", "ヒラギノ角ゴ ProN W3", sans-serif;
  color: #24292e;
}

* {
  box-sizing: border-box;
}

img {
  max-width: 100%;
}

picture > img {
  vertical-align: bottom;
}

.p-index__mv {
  height: 420px;
  margin-bottom: 87px;
}
.p-index__mv img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.p-index__works {
  max-width: 960px;
  padding: 0 60px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 137px;
}
@media (max-width: 767px) {
  .p-index__works {
    padding: 0 15px;
  }
}
.p-index__news {
  max-width: 960px;
  padding: 0 60px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 205px;
}
@media (max-width: 767px) {
  .p-index__news {
    padding: 0 15px;
  }
}
.p-index__link {
  text-align: center;
  margin-top: 54px;
}
.p-index__link > a {
  color: #24292e;
}
.p-index__link > a:hover {
  text-decoration: none;
}

